/* Css for main container */
.tool-palette {
    position: absolute;
    right: 40px;
    top: 25px;
    text-align: center;
    width: 225px;
    display: block;
    z-index: 1000;
    border-style: solid;
    border-width: 0px;
    border-color: #bbb;
    border-radius: 5px;
    color: black;
    user-select: none;
    /* supported by Chrome and Opera */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
}

/* Css for header compartment */

.palette-header {
    padding: 0.4em;
    text-align: left;
    background: #cccccc;
    border: 1px solid rgba(60, 60, 60, 0.6);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.header-icon {
    display: flex;
    align-items: center;
}

.header-icon i {
    margin-right: 0.2em;
}

.header-tools {
    display: flex;
    align-items: center;
}

.header-tools i {
    border: 1px solid #cccccc;
    padding: 0.15em;
    margin-right: 0.15em;
}

.header-tools i:hover {
    background: #dfdfdf;
}

.header-tools .clicked {
    background: #bddaef;
    border: 1px solid rgba(60, 60, 60, 0.6);
}

/*Css for palette body compartment */

.palette-body {
    background: rgba(100, 100, 100, 0.2);
    border: 1px solid rgba(60, 60, 60, 0.6);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.tool-group {
    text-align: left;
    background: #ededee;
}

.group-header {
    background: #cccccc;
    display: flex;
    align-items: center;
}

.group-header:hover {
    background: #aaaaaa;
}

.group-header i {
    padding: 0.4em;
}

.tool-button {
    background: #ededee;
    padding: 0.4em;
    display: flex;
    align-items: center;
}

.tool-button i {
    margin-right: 0.4em;
}

.tool-button:hover {
    background: #dfdfdf;
}

.tool-button.clicked {
    background: #bddaef;
}

.tool-button.collapsed {
    display: none;
}

.collapsible-palette {
    overflow: auto;
    transition: max-height 0.2s ease-out;
}

.minimize-palette-button {
    position: absolute;
    right: 20px;
    top: 35px;
    z-index: 1000;
}

.search-input {
    background: #dfdfdf;
    color: black;
    border: #bddaef;
    padding-left: 3px;
    width: 100%;
    margin: 3px;
}
